body {
  margin: 0;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  color: #232323;
  background: #f0beb6;
}

.title {
  font-family: "DM Serif Text", serif;
  text-align: center;
}

.wrapper {
  max-inline-size: 43.75rem;
  margin: 0 auto;
  padding-inline: 1rem;
  box-sizing: border-box;
}

.header {
  padding-block: 1rem;
  background: #47344b;
  color: #F5F5F5;
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header__name {
  text-transform: uppercase;
  font-family: "Roboto", sans-serif;
  font-size: 0.875rem;
}
@media (min-width: 1024px) {
  .header {
    grid-column: 1/-1;
    grid-row: 1;
    padding-block: 2rem;
  }
  .header__content {
    max-inline-size: none;
    padding-inline: 3rem;
    margin: 0 auto;
  }
  .header__name {
    font-size: 1rem;
  }
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-block: 2rem;
}
.form__campo {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.form__input, .form__textarea {
  padding: 0.5rem 0.5rem;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  border-radius: 0.5rem;
  border: none;
}
.form__input:focus, .form__textarea:focus {
  outline: #47344b 1px solid;
}
.form__buttons {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-block-start: 2rem;
}
.form__buttons > * {
  flex: 1;
}
.form__buttonReset {
  padding: 0.625rem 0.5rem;
  border-radius: 0.5rem;
  color: #47344b;
  background: #F5F5F5;
  border: 1px solid #47344b;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  cursor: pointer;
  position: relative;
}
.form__buttonSend {
  padding: 0.625rem 0.5rem;
  border-radius: 0.5rem;
  color: #F5F5F5;
  background: #47344b;
  border: 1px solid #47344b;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  cursor: pointer;
}
.form__buttonSend:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.form__buttonSend:disabled:hover::before {
  content: "sisi";
  display: block;
}
.form__error {
  color: red;
  margin: 0;
  padding-inline-start: 0.5rem;
  font-size: 0.875rem;
}
.form__campError {
  border: 1px solid red;
}
@media (min-width: 768px) {
  .form__buttonReset, .form__buttonSend {
    padding-block: 1rem;
    font-size: 1.125rem;
  }
}

.sk-folding-cube {
  margin: 20px auto;
  width: 40px;
  height: 40px;
  position: relative;
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
}

.sk-folding-cube .sk-cube {
  float: left;
  width: 50%;
  height: 50%;
  position: relative;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

.sk-folding-cube .sk-cube:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #47344b;
  -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
  animation: sk-foldCubeAngle 2.4s infinite linear both;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
}

.sk-folding-cube .sk-cube2 {
  -webkit-transform: scale(1.1) rotateZ(90deg);
  transform: scale(1.1) rotateZ(90deg);
}

.sk-folding-cube .sk-cube3 {
  -webkit-transform: scale(1.1) rotateZ(180deg);
  transform: scale(1.1) rotateZ(180deg);
}

.sk-folding-cube .sk-cube4 {
  -webkit-transform: scale(1.1) rotateZ(270deg);
  transform: scale(1.1) rotateZ(270deg);
}

.sk-folding-cube .sk-cube2:before {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}

.sk-folding-cube .sk-cube3:before {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
}

.sk-folding-cube .sk-cube4:before {
  -webkit-animation-delay: 0.9s;
  animation-delay: 0.9s;
}

@-webkit-keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
@keyframes sk-foldCubeAngle {
  0%, 10% {
    -webkit-transform: perspective(140px) rotateX(-180deg);
    transform: perspective(140px) rotateX(-180deg);
    opacity: 0;
  }
  25%, 75% {
    -webkit-transform: perspective(140px) rotateX(0deg);
    transform: perspective(140px) rotateX(0deg);
    opacity: 1;
  }
  90%, 100% {
    -webkit-transform: perspective(140px) rotateY(180deg);
    transform: perspective(140px) rotateY(180deg);
    opacity: 0;
  }
}
@media (min-width: 1024px) {
  .layout {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto 1fr;
    block-size: 100vh;
    background-position: left bottom;
    background-size: 35% 90%;
    background-repeat: no-repeat;
    background-color: #47344b;
  }
  @supports (background-image: url("../img/dog.png")) {
    .layout {
      background-image: url("../img/dog.png");
    }
  }
  @supports (background-image: url("../img/dog.webp")) {
    .layout {
      background-image: url("../img/dog.webp");
    }
  }
  @supports (background-image: url("../img/dog.avif")) {
    .layout {
      background-image: url("../img/dog.avif");
    }
  }
}

.main {
  grid-column: 2;
  grid-row: 2;
  background: #f0beb6;
  position: relative;
}

.sucess {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  background: #F5F5F5;
  visibility: hidden;
  padding: 0.875rem 0.5rem;
  border-radius: 0.8rem;
  text-align: center;
  max-inline-size: 31.25rem;
  min-inline-size: 15.625rem;
  position: absolute;
  inset-block-start: 50%;
  inset-inline-start: 50%;
  transform: translateX(-50%) translateY(-50%);
  box-shadow: 4px 4px 20px #232323;
}
.sucess__title, .sucess__text {
  margin: 0;
}

.sucess.active {
  visibility: visible;
}